.main-gallery {
  padding-bottom: 52px;

  @media screen and (min-width: 1440px) {
    padding-bottom: 100px;
  }
}
.main-gallery__title {
  @include font(24px, 700, 1.25, 0);
  color: var(--accent-color);

  margin-bottom: 16px;

  @media screen and (min-width: 768px) {
    @include font(36px, 700, 1.25, 0);
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1440px) {
    @include font(48px, 700, 1.25, 0);
    margin-bottom: 52px;
  }
}

.main-gallery__list {
  @extend %list;

  @media screen and (min-width: 320px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media screen and (min-width: 600px) {
    margin-top: -5px;
    margin-left: -5px;
  }

  @media screen and (min-width: 768px) {
    margin-top: -18px;
    margin-left: -18px;
  }

  @media screen and (min-width: 1440px) {
    margin-top: -32px;
    margin-left: -32px;
  }
}

.main-gallery__item {
  @media screen and (max-width: 599px) {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  @media screen and (min-width: 600px) {
    margin-top: 5px;
    margin-left: 5px;
    flex-basis: calc((100% - 10px) / 2);
  }

  @media screen and (min-width: 768px) {
    margin-top: 18px;
    margin-left: 18px;
    flex-basis: calc((100% - 36px) / 2);
  }

  @media screen and (min-width: 768px) and (max-width: 1439px) {
    max-width: 350px;
  }

  @media screen and (min-width: 1440px) {
    margin-top: 32px;
    margin-left: 32px;
    flex-basis: calc((100% - 64px) / 2);
  }
}

.main-gallery__image {
  max-width: 100%;

  @media screen and (min-width: 320px) {
    margin-bottom: 8px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 16px;
  }
}

.main-gallery__text-wrapper {
  @media screen and (min-width: 320px) {
    max-width: 280px;
  }

  @media screen and (min-width: 768px) {
    max-width: 350px;
  }

  @media screen and (min-width: 1440px) {
    max-width: 515px;
  }
}

.main-gallery__text {
  @include font(16px, 300, 1.25, 0);
  color: var(--accent-color);

  @media screen and (min-width: 1440px) {
    @include font(17px, 300, 1.27, 0);
  }
}
